import { Section } from "../../components/Section";
import { moviesApi } from "../../api";
import { useEffect, useState } from "react";
import { PageLoading } from "../../components/PageLoading";
import { Main } from "./Main";
import { PageError } from "./PageError";
import { Content } from "./Content";

export const Home = () => {
  const [nowPlay, setNowPlay] = useState();
  const [pop, setPop] = useState();
  const [coming, setComing] = useState();
  const [rated, setRated] = useState();
  const [loading, setLoading] = useState(true);
  const [pageError, setPageError] = useState(false);

  useEffect(() => {
    const movieData = async () => {
      try {
        const {
          data: { results: nowPlaying },
        } = await moviesApi.nowPlaying();
        setNowPlay(nowPlaying);

        const {
          data: { results: popular },
        } = await moviesApi.popular();
        setPop(popular);

        const {
          data: { results: coming },
        } = await moviesApi.upcoming();
        setComing(coming);

        const {
          data: { results: topRated },
        } = await moviesApi.top_rated();
        setRated(topRated);

        setLoading(false);
      } catch (error) {
        setPageError(true);
        console.log(error);
      }
    };
    movieData();
  }, []);

  // console.log("í˜„ì¬ìƒì˜", nowPlay);
  // console.log("ì¸ê¸°", pop);
  // console.log("ìƒì˜ì˜ˆì •", coming);
  // console.log("ìˆœìœ„", rated);

  return (
    <div>
      {loading ? (
        <PageLoading />
      ) : (
        <div>
          {pageError ? (
            <PageError />
          ) : (
            <div>
              {pop ? <Main pop={pop[0]} /> : null}

              <Section>
                <Content title="í˜„ì¬ ìƒì˜ ì˜í™”" nowPlay={nowPlay} />
                <Content title="ìƒì˜ ì˜ˆì •" nowPlay={coming} />
                <Content title="ì¸ê¸° ì˜í™”" nowPlay={pop} />
                <Content title="ì˜í™” ìˆœìœ„" nowPlay={rated} />
              </Section>
            </div>
          )}
        </div>
      )}
    </div>
  );
};
